// Module
___CSS_LOADER_EXPORT___.push([module.id, `.card-validator-widget {
  font-family: Arial, sans-serif;
  max-width: 350px;
  margin: 20px auto;
  padding: 15px;
  border: 1px solid #ccc;
  border-radius: 5px;
  background-color: #fff;
  box-shadow: 0 2px 8px rgba(0,0,0,0.1);
}

.card-icons {
  display: flex;
  justify-content: flex-start;
  margin-bottom: 15px;
  gap: 10px;
}

.card-icon {
  width: 50px;
  height: 30px;
  object-fit: contain;
  cursor: default;
  user-select: none;
  opacity: 0.6; /* Сделано менее прозрачным */
  border-radius: 4px;
  transition: opacity 0.3s ease, box-shadow 0.3s ease, border 0.3s ease;
}

/* Подсветка активной иконки */
.card-icon.active {
  opacity: 1;
  box-shadow: 0 0 8px 2px #4caf50;
  border: 2px solid #4caf50;
  background-color: #e8f5e9;
}

.card-input {
  width: 100%;
  padding: 8px;
  font-size: 1rem;
  margin-bottom: 10px;
  box-sizing: border-box;
  border: 1px solid #ccc;
  border-radius: 3px;
  transition: border-color 0.3s ease;
}

.card-input:focus {
  border-color: #4CAF50;
  outline: none;
}

.validate-button {
  background-color: #4CAF50;
  color: white;
  border: none;
  padding: 8px 12px;
  font-size: 1rem;
  cursor: pointer;
  border-radius: 3px;
  transition: background-color 0.3s ease;
}

.validate-button:hover {
  background-color: #45a049;
}

.validation-result {
  margin-top: 10px;
  font-weight: bold;
  min-height: 1.2em;
  color: #333;
}
